import * as React from 'react';
import Typography from '@material-ui/core/Typography';
import { makeStyles, createStyles, useTheme } from '@material-ui/core/styles';
import { Chart, BarSeries, Title, ArgumentAxis, ValueAxis, Legend } from '@devexpress/dx-react-chart-material-ui';
import { useDecades } from '../utils/graphql-hooks';

const useTitleStyles = makeStyles((theme) =>
  createStyles({
    root: {
      width: '100%',
      textAlign: 'center',
      padding: theme.spacing(2),
    },
  })
);

function TitleComponent({ text }: Title.TextProps) {
  const classes = useTitleStyles();
  return (
    <div className={classes.root}>
      <Typography variant="h6" component="h3">
        {text}
      </Typography>
    </div>
  );
}

export default function DecadesRank() {
  const { palette } = useTheme();
  const decades = useDecades();
  return (
    <div>
      <Chart data={decades}>
        <ArgumentAxis />
        <ValueAxis />
        <BarSeries valueField="value" argumentField="fieldValue" color={palette.primary.main} />
        <Title text="年代別曲数" textComponent={TitleComponent} />
      </Chart>
    </div>
  );
}
